/* raleway-regular - latin_cyrillic */
@font-face {
    font-family: "Raleway";
    font-style: normal;
    font-weight: 400;
    src: url("../Fonts/raleway-v27-latin_cyrillic-regular.eot"); /* IE9 Compat Modes */
    src: local(""), url("../Fonts/raleway-v27-latin_cyrillic-regular.eot?#iefix") format("embedded-opentype"),
        /* IE6-IE8 */ url("../Fonts/raleway-v27-latin_cyrillic-regular.woff2") format("woff2"),
        /* Super Modern Browsers */ url("../Fonts/raleway-v27-latin_cyrillic-regular.woff") format("woff"),
        /* Modern Browsers */ url("../Fonts/raleway-v27-latin_cyrillic-regular.ttf") format("truetype"),
        /* Safari, Android, iOS */ url("../Fonts/raleway-v27-latin_cyrillic-regular.svg#Raleway") format("svg"); /* Legacy iOS */
}
